* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.App {
  min-height: 100vh;
  padding: 1rem 0.75rem;
  font-family: sans-serif;
  background-color: #eaffd0;
}

@media (min-width: 400px) {
  .App {
    padding: 1rem 2rem 2rem;
  }
}

p {
  line-height: 1.6;
}
select {
  padding: 0.5rem;
  border-radius: 0.25rem;
  font-size: 1rem;
}
button {
  border: 1px solid black;
  border-radius: 0.25rem;
  padding: 0.25rem 0.5rem;
  font-size: 1rem;
}
button:not(.unavailable) {
  cursor: pointer;
}
button:not(.unavailable):hover {
  background-color: #f38181;
}

.line-1 {
  display: flex;
  align-items: center;
}
.line-1 > p {
  margin-right: 0.5rem;
}
.line-1 > input {
  width: 4rem;
  border-radius: 0.25rem;
  padding: 0.5rem;
  font-size: 1rem;
  border: 1px solid black;
}

.hide-sb {
  overflow: auto;
  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none; /* Firefox */
}
.hide-sb::-webkit-scrollbar {
  display: none; /* Chrome, Safari and Opera */
}
.unavailable {
  opacity: 0.8;
}
.full-w {
  width: 100%;
}
.pointer {
  cursor: pointer;
}

.flex {
  display: flex;
}
.flex-wrap {
  display: flex;
  flex-wrap: wrap;
}
.flex-center {
  display: flex;
  justify-content: center;
  align-items: center;
}
.flex-col {
  display: flex;
  flex-direction: column;
}

.justify-center {
  justify-content: center;
}
.justify-between {
  justify-content: space-between;
}
.justify-around {
  justify-content: space-around;
}
.justify-evenly {
  justify-content: space-evenly;
}
.justify-end {
  justify-content: flex-end;
}

.align-center {
  align-items: center;
}
.align-start {
  align-items: flex-start;
}
.align-end {
  align-items: flex-end;
}

